import store from "./store/index";

export const getStage = (val) => {
    let stage  = ""
    switch(val) {
        case "1": stage = 'الصف الأول الاعدادي';
        break;
        case "2": stage = 'الصف الثاني الاعدادي';
        break;
        case "3": stage = 'الصف الثالث الاعدادي';
        break;
        case "4": stage = 'الصف الأول الثانوي';
        break;
        case "5": stage = 'الصف الثاني الثانوي';
        break;
        case "6": stage = 'الصف الثالث الثانوي';
        break;
      }
      return stage;
}

export const getSection = (val, stage) => {
  const section = store.getters.sections.find((sec) => sec.number == val && sec.stage == stage);
  if(section) {
    return section.name;
  } else return val;
}

export const generateExamModel = (exam) => {
  const solutionModel = exam.sections.map((section) => {
    return {
      type: section.type,
      questions: section.questions.map((question) => {
        return {
          question: question.question,
          choices: question.choices,
          answer: "",
          degree: 0,
          fullDegree: question.fullDegree,
          correction: "",
        };
      }),
    };
  });
  return solutionModel;
}

export const shuffleArray = (arr) => {
  const questions = [];
  do {
    const i = Math.floor(Math.random() * arr.length);
    if(!questions.includes(arr[i])) questions.push(arr[i]);
  }
  while(arr.length != questions.length);
  return questions;
}

export const generateRandomString = (len) => {
  const allCapsAlpha = [..."ABCDEFGHIJKLMNOPQRSTUVWXYZ"]; 
  const allLowerAlpha = [..."abcdefghijklmnopqrstuvwxyz"]; 
  const allUniqueChars = [..."~!@#$%^&*()_+-=[]\{}"];
  const allNumbers = [..."0123456789"];

  const base = [...allCapsAlpha, ...allNumbers, ...allLowerAlpha];

    return [...Array(len)]
      .map(i => base[Math.random()*base.length|0])
      .join('');
  };


  export const getAlignStyle = () => {
    const lang = store.getters.adminData.language || "arabic";
    if(lang == 'arabic') return "text-align: left; direction: ltr;";
    else return "text-align: right; direction: rtl;";
  }

  export const delay = async(time) => {
    return new Promise((res) => {
      setTimeout(() => {
        res();
      }, time);
    })
  }

  export const setEmbedUrl =(val) => {
      ////////////////////////////////////////////////////
      if (!val || val == "" || val == "null" || val == undefined || val == "undifined")
        return "";
      if (val.includes("vimeo")) return val;
      const slashes = val.split("/");
      const length = slashes.length;
      let id = "";
      if (val.includes("youtube") && val.includes("/watch")) {
        id = val.split("v=")[1];
      } else if (
        val.includes("youtube") &&
        (val.includes("/shorts") || val.includes("/video")) &&
        val.includes("/edit")
      ) {
        id = slashes[length - 2];
      } else if (
        val.includes("youtube") &&
        (val.includes("/shorts") || val.includes("/video") || val.includes("/embed"))
      ) {
        id = slashes[length - 1];
      } else if (val.includes("youtu.be")) {
        id = slashes[length - 1];
      } else {
        return "";
      }
      id = id.split("?").length > 0 ? id.split("?")[0] : id;
      return `https://www.youtube.com/embed/${id}`;
  }