<template>
  <div class="bg-light">
    <h1 class="page-title">ملف الطالب</h1>
    <hr />
    <br />

    <!--------------------Exams------------------->
    <div class="container">
      <div class="_sec w-100">
        <responsive-table
          title="معلومات الطالب"
          :items="userData"
          :cols="['fullname', 'stage', 'email', 'address', 'phone', 'parentPhone']"
          :colsTitles="[
            'الأسم',
            'المرحلة',
            'الأيميل',
            'السنتر والميعاد',
            'التليفون',
            'تليفون ولي الامر',
          ]"
          :colsTypes="[
            'readonly',
            'readonly',
            'readonly',
            'readonly',
            'readonly',
            'readonly',
          ]"
          :loading="laodingUserData"
          color="#222222"
          :btns="[]"
        />
      </div>
      <!-- <hr /> -->

      <!--------------------Exams------------------->
      <div class="_sec">
        <responsive-table
          title="الدروس التي تم حضورها"
          :items="lessons"
          :cols="['section', 'name', 'number']"
          :colsTitles="['الوحدة', 'أسم الدرس', 'رقم الدرس']"
          :colsTypes="['readonly', 'readonly', 'readonly']"
          :loading="laodingLessons"
          color="#20688b"
          :btns="[
            role == 1 && {
              icon: 'list',
              method: (less) => $router.push('/dashboard/uploadVideo/' + less._id),
            },
          ]"
        />
      </div>
      <!-- <hr /> -->

      <!--------------------Tasks--------------------->
      <div class="_sec">
        <responsive-table
          title="الواجبات"
          :items="taskSolutions"
          :cols="['section', 'lesson', 'name', 'stage', 'totalDegree', 'fullDegree']"
          :colsTitles="[
            'الوحدة',
            'رقم الدرس',
            'الأسم',
            'المرحلة',
            'درجة الطالب',
            'الدرجة النهائية',
          ]"
          :colsTypes="[
            'readonly',
            'readonly',
            'readonly',
            'readonly',
            'readonly',
            'readonly',
          ]"
          :loading="laodingTaskSolutions"
          color="#7c3240"
          :btns="[]"
        />
        <br />
        <progress-bar
          :degree="tasksTotalDegree"
          :fullDegree="tasksFullDegree"
          caption="مجموع درجات الواجبات"
          color="green"
        />
      </div>
      <!-- <hr /> -->

      <!--------------------Exams--------------------->
      <div class="_sec">
        <responsive-table
          title="الأمتحانات"
          :items="examSolutions"
          :cols="['unit', 'name', 'number', 'stage', 'totalDegree', 'fullDegree']"
          :colsTitles="[
            'الوحدة',
            'الأسم',
            'رقم الأمتحان',
            'المرحلة',
            'درجة الطالب',
            'الدرجة النهائية',
          ]"
          :colsTypes="[
            'readonly',
            'readonly',
            'readonly',
            'readonly',
            'readonly',
            'readonly',
          ]"
          :loading="laodingExamSolutions"
          color="#26304a"
          :btns="[
            role == 1 && {
              icon: 'list',
              method: (sol) => $router.push('/dashboard/answers/solution/' + sol._id),
            },
          ]"
        />
        <br />
        <progress-bar
          :degree="examsTotalDegree"
          :fullDegree="examsFullDegree"
          caption="مجموع درجات الأمتحانات"
          color="#115885"
        />
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { getStage, getSection } from "../functions";
export default {
  async created() {
    let userId;
    if (this.$route.params.userId) userId = this.$route.params.userId;
    else userId = this.$store.getters.userId;
    this.userId = userId;
    /////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////

    this.laodingUserData = true;
    const res = await axios.get("/getUserData/" + this.userId);
    this.laodingUserData = false;
    this.stage = res.data.userData[0].stage;
    this.userData = res.data.userData.map((ud) => {
      return { ...ud, stage: getStage(ud.stage) };
    });
    /////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////

    this.laodingLessons = true;
    axios.get("/getLessonsPlayedByUser/" + this.userId).then((res) => {
      this.laodingLessons = false;
      this.lessons = res.data.lessons.map((less) => {
        return {
          ...less,
          stage: getStage(less.stage),
          section: getSection(less.section, less.stage),
        };
      });
    });
    /////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////

    this.laodingExamSolutions = true;
    axios.get("/getUserUnitsSolutions/" + this.userId).then((res) => {
      this.laodingExamSolutions = false;
      this.examSolutions = res.data.solutions
        .filter((sol) => !sol.isTask)
        .map((sol) => {
          return {
            ...sol,
            stage: getStage(sol.stage),
            section: getSection(sol.section, sol.stage),
          };
        });
      ///////////////////////////
      this.taskSolutions = res.data.solutions
        .filter((sol) => sol.isTask)
        .map((sol) => {
          return {
            ...sol,
            stage: getStage(sol.stage),
            section: getSection(sol.section, sol.stage),
          };
        });
    });
    /////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////

    axios
      .post(`/getUnitExamsFullDegree`, {
        stage: this.$store.getters.stage,
        isTask: false,
      })
      .then((res) => (this.examsFullDegree = res.data.fullDegree));
    axios
      .post(`/getUnitExamsFullDegree`, { stage: this.$store.getters.stage, isTask: true })
      .then((res) => (this.tasksFullDegree = res.data.fullDegree));
  },
  data() {
    return {
      stage: null,
      lessons: [],
      taskSolutions: [],
      examSolutions: [],
      userData: [],
      laodingUserData: false,
      laodingLessons: false,
      laodingTaskSolutions: false,
      laodingExamSolutions: false,
      examsFullDegree: 1,
      tasksFullDegree: 1,
    };
  },
  computed: {
    tasksTotalDegree() {
      return this.taskSolutions.reduce((a, b) => {
        return a + b.totalDegree;
      }, 0);
    },
    examsTotalDegree() {
      return this.examSolutions.reduce((a, b) => {
        return a + b.totalDegree;
      }, 0);
    },
    tasksFullDegree() {
      return this.taskSolutions.reduce((a, b) => {
        return a + b.fullDegree;
      }, 0);
    },
    // examsFullDegree() {
    //   return this.examSolutions.reduce((a, b) => {
    //     return a + b.fullDegree;
    //   }, 0);
    // },
    role() {
      return this.$store.getters.role;
    },
  },
};
</script>

<style lang="scss" scoped>
._cont {
  width: 80%;
  margin: 2px auto;
  @media (max-width: 700px) {
    width: 100%;
  }
}
._sec {
  margin-bottom: 80px;
  border-bottom: 2px solid #444;
  border-radius: 12px;
  padding: 2px;
  &:nth-child(odd) {
    background: rgb(223, 223, 223);
  }
}
</style>
